import Vue from 'vue'
import Router from 'vue-router'
import '../common/style/common.css'
import '../common/style/reset.css'

Vue.use(Router)

// 大背景页
const Main = (resolve) => {
  import('@/main/main.vue').then((module) => {
    resolve(module)
  })
}

// 首页
const homePage = (resolve) => {
  import('views/homePage/homePage.vue').then((module) => {
    resolve(module)
  })
}

// 第二页
const second = (resolve) => {
  import('views/second/second.vue').then((module) => {
    resolve(module)
  })
}

export default new Router({
  routes: [
    {
      path: '/',
      component: Main,
      children: [
        {
          path: '/',
          name: 'homePage',
          component: homePage
        },
        {
          path: '/second',
          name: 'second',
          component: second
        }
      ]
    }
  ]
})



